.pInfo{
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0,0, 0.2);
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
}
.pContainer{
    flex: 1;
    display: flex;
    margin: 5px;
    min-width: 280px;
    height: 350px;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: #f5fbfd;
    transition: all 0.5s ease;
    cursor: pointer ;
&:hover{
    background-color: #e9f5f5;
    transform: scale(1.1);
}
    
&:hover .pInfo{
    opacity: 1;
};

    
}

.pimg{
    height: 75%;
    z-index: 2;
   
}

.pcircle{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: absolute;
    background-color: white;
}

.picon{
width: 40px;
height: 40px;
border-radius: 50%;
background-color: #fff;
display: flex;
align-items: center;
justify-content: center;
margin: 10px;
transition: all 0.5s ease;
&:hover{
    background-color: #e9f5f5;
    transform: scale(1.1);
}
}