.RegContainer{
    width: 100vw;
    height: 100vh;
    background:linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5)), url("https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    display: flex;
    align-items: center;
    justify-content: center;
     
}
.RegWraper{
    width: 40%;
    padding: 20px;
    background-color: #fff;
}
@media only screen and (max-width: 450px) {
    .RegWraper {
        width: 75%;}
    }
.regTitle{
    font-size: 24px;
    font-weight: 300;

}
form{
    display: flex;
    flex-wrap: wrap;

}
.regInput{
    flex: 1;
    min-width: 40%;
    margin: 20px 10px 0px 0px;
    padding: 10px; 
}
.AGREEMENT{
    font-size: 12px;
    margin: 20px 0px;

}
.cra{
    width: 40%;
    bottom: none;
    padding: 15px 20px;
    border-radius: 10px;
    transition: all 0.5s ease;
    &:hover{
        background-color: teal;
        color: white;
        cursor: pointer;
    }

}